:root {
  --container: clamp(0px,83.611vw,1204px);
}

:root {
  --color-primary: #7e58e1;
  --color-primary-variant: #7e58e1;
  --color-secondary: #83d2b8;
  --color-background: #fff;
  --color-surface: #f9f8f8;
  --color-surface-text: #222222;
  --color-surface-border: #222222;
  --color-surface-field: #222222;
  --color-base-text: rgba(0, 0, 0, 0.85);
  --color-base-link: #4ebe99;
  --color-base-link-hover: #4ebe99;
  --color-base-link-decoration: currentColor;
  --color-base-heading: #01283c;
  --color-base-border: #cccccc;
  --color-form-base-border: #cccccc;
  --color-form-base-border--focus: #dddddd;
  --color-form-base-field: #fff;
  --color-price: #000000;
  --color-price-old: #9c9fa3;
  --color-price-factory: #989898;
  --color-price-specoffer: #e11515;
  --color-status-stock-available: #25d366;
  --color-status-delivery-time: #6e7782;
  --color-status-expected: #ef9500;
  --color-status-success: #1bae70;
  --color-status-error: #f03748;
  --color-badge-best-choose: #ff5c00;
  --color-badge-new: #ef1616;
  --color-badge-discount-percent: #3b37ff;
  --color-badge-specoffer: #ffa800;
  --color-badge-sale: #7360f2;
  --Dark-bluish-green-100: #01283c;
  --Sky-blue-100: #12c3f4;
  --Yellowgreen-100: #16322c;
  --Yellowgreen-90: #a6d04f;
  --Yellowgreen-80: #b0d562;
  --Yellowgreen-70: #badb76;
  --Yellowgreen-60: #c4e089;
  --Yellowgreen-50: #cee59d;
  --Yellowgreen-40: #d7eab1;
  --Yellowgreen-30: #e1efc4;
  --Yellowgreen-20: #ebf5d8;
  --Yellowgreen-10: #f5faeb;
  --Shamrock-100: #4ebe99;
  --Shamrock-90: #60c5a3;
  --Shamrock-80: #71cbad;
  --Shamrock-70: #83d2b8;
  --Shamrock-60: #95d8c2;
  --Shamrock-50: #a7dfcc;
  --Shamrock-40: #b8e5d6;
  --Shamrock-30: #caece0;
  --Shamrock-20: #dcf2eb;
  --Shamrock-10: #edf9f5;
}

:root {
  --font-family: "Inter", sans-serif;
  --second-family: "SF Pro Text", sans-serif;
  --third-family: "Montserrat", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 100%;
  scroll-behavior: smooth;
}

body {
  text-rendering: optimizeLegibility;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  min-width: 320px;
  overflow: auto;
}

pre {
  white-space: pre-line;
}

.clear {
  clear: both;
}

a {
  outline: none;
}

a:active {
  outline: none;
}

a:focus {
  outline: none;
}

ol > *:first-child,
ul > *:first-child {
  margin-top: 0 !important;
}

ol > *:nth-last-child(-n + 1),
ul > *:nth-last-child(-n + 1) {
  margin-bottom: 0 !important;
}

ul li {
  list-style: none;
  position: relative;
}

img {
  max-width: 100%;
  height: auto;
}

hr {
  border: none;
  width: 100%;
}

table {
  width: 100%;
  border-collapse: collapse;
  border-style: solid;
}

table th,
table td {
  vertical-align: top;
}

.table-nostyle {
  border: none;
}

.table-nostyle thead {
  background-color: transparent;
}

.table-nostyle tr {
  background-color: transparent;
}

.table-nostyle tr th,
.table-nostyle tr td {
  border: none;
  padding: 0;
  line-height: inherit !important;
}

.table-wrapper {
  overflow: auto;
}

button {
  outline: 0;
}

button:focus {
  outline: none;
}

.btn,
.to-basket .form-actions,
.form-submit,
.edit-submit,
.ui-dialog-buttonpane button {
  cursor: pointer;
  outline: none;
  user-select: none;
  position: relative;
  transition: all 0.2s ease-in-out;
  color: inherit;
}

.btn:focus {
  box-shadow: none;
}

input[type="tel"],
input[type="text"],
input[type="password"],
input[type="email"],
input[type="number"],
input[type="date"],
input[type="time"] {
  display: block;
  width: 100%;
  outline: none;
  transition: all 0.2s ease-in-out;
}

input[type="tel"]::-webkit-input-placeholder,
input[type="text"]::-webkit-input-placeholder,
input[type="password"]::-webkit-input-placeholder,
input[type="email"]::-webkit-input-placeholder,
input[type="number"]::-webkit-input-placeholder,
input[type="date"]::-webkit-input-placeholder,
input[type="time"]::-webkit-input-placeholder {
  opacity: 1;
}

input[type="tel"]::-moz-placeholder,
input[type="text"]::-moz-placeholder,
input[type="password"]::-moz-placeholder,
input[type="email"]::-moz-placeholder,
input[type="number"]::-moz-placeholder,
input[type="date"]::-moz-placeholder,
input[type="time"]::-moz-placeholder {
  opacity: 1;
}

input[type="tel"]:-moz-placeholder,
input[type="text"]:-moz-placeholder,
input[type="password"]:-moz-placeholder,
input[type="email"]:-moz-placeholder,
input[type="number"]:-moz-placeholder,
input[type="date"]:-moz-placeholder,
input[type="time"]:-moz-placeholder {
  opacity: 1;
}

input[type="tel"]:-ms-input-placeholder,
input[type="text"]:-ms-input-placeholder,
input[type="password"]:-ms-input-placeholder,
input[type="email"]:-ms-input-placeholder,
input[type="number"]:-ms-input-placeholder,
input[type="date"]:-ms-input-placeholder,
input[type="time"]:-ms-input-placeholder {
  opacity: 1;
}

input[type="tel"]:focus,
input[type="text"]:focus,
input[type="password"]:focus,
input[type="email"]:focus,
input[type="number"]:focus,
input[type="date"]:focus,
input[type="time"]:focus {
  outline: 0;
}

input[type="tel"]:disabled,
input[type="tel"][readonly],
input[type="text"]:disabled,
input[type="text"][readonly],
input[type="password"]:disabled,
input[type="password"][readonly],
input[type="email"]:disabled,
input[type="email"][readonly],
input[type="number"]:disabled,
input[type="number"][readonly],
input[type="date"]:disabled,
input[type="date"][readonly],
input[type="time"]:disabled,
input[type="time"][readonly] {
  opacity: 1;
}

textarea {
  width: 100%;
  resize: none;
  outline: none;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

input[type='number'],
input[type="number"]:hover,
input[type="number"]:focus {
  appearance: none;
  -moz-appearance: textfield;
}

input[type="date"].form-control,
input[type="time"].form-control,
input[type="datetime-local"].form-control,
input[type="month"].form-control {
  appearance: none;
}

.webform-confirmation__back {
  display: none;
}

form [style=""],
form [id^="ajax-wrapper"] {
  display: contents;
}

form .ajax-new-content {
  display: none !important;
}

.iti {
  display: block;
}

.select2-container--default .select2-selection--single {
  outline: none !important;
}

.sfi-wrapper {
  width: 100% !important;
  outline: none !important;
}

.imce-upload-form input[type="file"] {
  display: block !important;
}

.rouble {
  font-family: Rouble;
}

.stretched-link::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  pointer-events: auto;
}

a.lightgallery {
  border-bottom: none;
}

.overlay {
  display: block !important;
  background-color: rgba(0, 0, 0, 0.6);
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
}

.overlay.is-open {
  opacity: 1;
  visibility: visible;
}

.dialog-off-canvas-main-canvas {
  overflow: clip;
}

